import React from "react";
import MainHOC from "../component/main.hoc";

const SearchJud = () => {
  return (
    <MainHOC>
      <div className="field is-horizontal">
        <div className="field-label is-normal">
          <label className="label">From</label>
        </div>
        <div className="field-body">
          <div className="field">
            <p className="control is-expanded has-icons-left">
              <input className="input" type="text" placeholder="Name" />
              <span className="icon is-small is-left">
                <i className="fas fa-user" />
              </span>
            </p>
          </div>
          <div className="field">
            <p className="control is-expanded has-icons-left has-icons-right">
              <input
                className="input is-success"
                type="email"
                placeholder="Email"
                defaultValue="alex@smith.com"
              />
              <span className="icon is-small is-left">
                <i className="fas fa-envelope" />
              </span>
              <span className="icon is-small is-right">
                <i className="fas fa-check" />
              </span>
            </p>
          </div>
        </div>
      </div>
      <div className="field is-horizontal">
        <div className="field-label" />
        <div className="field-body">
          <div className="field is-expanded">
            <div className="field has-addons">
              <p className="control">
                <a className="button is-static">+44</a>
              </p>
              <p className="control is-expanded">
                <input
                  className="input"
                  type="tel"
                  placeholder="Your phone number"
                />
              </p>
            </div>
            <p className="help">Do not enter the first zero</p>
          </div>
        </div>
      </div>
      <div className="field is-horizontal">
        <div className="field-label is-normal">
          <label className="label">ประเภทเงิน</label>
        </div>
        <div className="field-body">
          <div className="field is-narrow">
            <div className="control">
              <div className="select is-fullwidth">
                <select>
                  <option>เลือกประเภทเงินเดือน</option>
                  <option value="1">ตกเบิก</option>
                  <option value="2">เงินเดือน</option>
                </select>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="field is-horizontal">
        <div className="field-label is-normal">
          <label className="label">ประจำเดือน</label>
        </div>
        <div className="field-body">
          <div className="field is-narrow">
            <div className="control">
              <div className="select is-fullwidth">
                <select>
                  <option>เลือกประเภทเงินเดือน</option>
                  <option value="1">ตกเบิก</option>
                  <option value="2">เงินเดือน</option>
                </select>
              </div>
            </div>
          </div>
        </div>
      </div>
    </MainHOC>
  );
};
export default SearchJud;
